        font-size: 2.5em;
    }
    .icon-menu {
        display: none;
    }
    .menu {
        position: static;
        display: flex;
        height: 60px;
        width: auto;
    }
    .menu__link {
        padding: 20px;
    }
    .group--color .container {
        margin-top: 30px;
        margin-bottom: 30px;
        padding: 20px;
    }
    .main__title {
        font-size: 2.3em;
    }
    .main__about__description .column--50:nth-child(2) .column__txt {
        line-height: 50px;
    }
    .today-special__title {
        font-size: 1.4em;
    }
    .main-footer {
        padding-top: 30px;
        padding-bottom: 30px;
    }
} 

@media screen and (main-width:1600px) {
    .container {
        width: 1400px;
    }
    .main__about__description .column:nth-child(1) img {
        widows: 100%;
        height: 350px;
        object-fit: cover;
    }
    .today-special__img {
        height: 300px;
    }
}





