@import url('https://fonts.googleapis.com/css?family=Monserrat:400,700|Pacifico');
* {
    box-sizing: border-box;
}


body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}
/*--Estils base----*/
img{
    display: block;
    width: 100%;
    max-width: 100%;
}
h1, h2, h3, h4, h5, h6 {
    margin: 0;  
}
.testimonios{
    text-align: center;
  }
  .our-team__img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
    margin: auto;
    margin-bottom: 20px;  
}  
.container{
    width: 100%;
    margin: auto;
}
.container--flex{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.column{
    width: 100%;
}
/*----Estilos Header ---*/
.main-header{
    width: 100%;   
}
.logo{
    font-size: 1.8em;
    color: #DE423A;
    padding-left: 30px;
    font-family: 'Pacifico', cursiva;
    font-weight: 100;
}
.main-header__contactInfo__phone {
    background: #DE423A;
    color: white;
    margin: 0 auto;
    padding-right: 120px;
}
.main-header__contactInfo__address {
    padding-right: 30px;
    margin: 0;   
}
.main-header [class*="icon-"]:before{
    position: relative;
    top: 2px;
    right: 5px;
}
/*----Estilos del menu ---*/
.main-nav {
    width: 100%;
    position: relative;
    z-index: 2000;
    padding: 10px;
}
.icon-menu{
    display: block;
    color: white;
    border: 1px solid white;
    border-radius: 3px;
    width: 40px;
    height: 40px;
    line-height: 45px;
    text-align: center;
    cursor: pointer;
    font-size: 1.5em;

}
.social-icon { 
    display: flex;
    justify-content: space-between;
}
.social-icon [class*="icon-"]{
    color: black;
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3em;
    width: 35px;
    height: 35px;
    background: white;
    border-radius: 50%;  
}
.social-icon__link {
    text-decoration: none;
}
.menu {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background: rgba(0,0,0,.75);
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    height: 0;
    overflow: hidden;
    transition: height .3s linear;

}
.menu__link {
    display: block;
    padding: 15px;
    color: white;
    text-decoration: none;
}
.menu__link:hover {
    background: #DE423A;
}
.menu__link--select {
    background: #DE423A;
}
.mostrar {
    height: 200px;
}

/*----Estilos del banner ---*/

.banner {
    margin-top: -55px;
    position: relative;
}
.banner::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 1000;
    top: 0;
}
.banner__img {
    width: 100%;
    height: 700px;
    object-fit: cover;
}
.banner__content {
    width: 90%;
    color: white;
    text-align: center;
    position: absolute;
    z-index: 1500;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    font-size: 1.5em;
    font-weight: bold;
}
/*----Estilos principales ---*/
.group--color .container {
    background: #DE423A;
    color: white;
    padding: 10px;
    text-align: center;
}
.main__title {
    margin: 15px 0;
    font-size: 1.8em;
    font-family: 'Pacifico', cursive;
    font-weight: 100;
}
.column__title {
    font-size: 1.3em;
}
.main__about__description .column:nth-child(2) {
    padding: 10px;
}
.btn {
    display: block;
    text-align: center;
    text-decoration: none;
    width: 128px;
    background: #DE423A;
    color: white;
    padding: 10px;
    margin: 10px auto;
}
.group--title{
    font-family: 'Pacifico',cursive;
    text-align: center;
    font-weight: 100;
    font-size: 1.8em;
    margin: 30px;
}
.today-special .column {
    margin-bottom: 30px;
}
.today-special__img {
    margin: auto;
    max-width: 350px;
}
.today-special__title {
    text-align: center;
    font-size: 1.3em;
    padding-top: 10px;
    padding-bottom: 20px;
}
.today-special__price {
    font-size: 1.5em;
    color: #DE423A;
    font-weight: bold;
    text-align: center;

}

/*-----*/
.title{
    font-family: 'Montserrat', sans-serif;
    font-size: 100px;
    text-align: center;
}
.colu{
    font-family: 'Montserrat', sans-serif;
    font-size: 70px;     
}

.colu h3{
    color: tomato;
}
.colu h4{
    color: black;
}


/*----Estilos footer ---*/
.mapadesitio{
    height: 200px;
    width: 350px;
}
/*----Estilos responsive ---*/
@media screen and (min-width:480px) {
    .logo {
        color: white;   
    }
    .main-header__contactInfo {
        text-align: right;
    }
    .banner {
        margin-top: -200px;
        z-index: -1000;
    }
    .main-header__contactInfo__phone {
        background: none;
        
    }
    .main-header__contactInfo__address {
        color: white;
    }
    .column--50 {
        width: 49%;
    }
    .main-nav {
        background: rgba(0,0,0,.75);
    }
    .banner {
        margin-top: -200px;
        z-index: -1000;
    }
    .banner__img {
        height: 600px;
    }
    .banner__content {
        font-size: 2em;
    }
    .main {
        padding-bottom: 15xp;
    }
    
    .main__about__description .column:nth-child(2) {
        padding-left: 20px;
        font-size: .9em;
    }
    .main__about__description .btn {
        margin: 0;
    }
    .today-special .column {
        border: 5px solid #ddd;
        padding: 5px;     
    }
    .today-special__img {
        height: 200px;
        object-fit: cover;

    }
    .main-footer .container--flex {
        align-items: flex-start;

    }
    .column--50 {
        width: 49%;
    }
    .column--50-25{
        width: 49%;
    }
    .column--33{
        width: 32%;
    }
}
@media screen and (min-width:768px) {
    .main__title {
        font-size: 2.2em;
    }
    .main__about__description {
        margin-top: 30px;
    }
    .main__about__description .column--50:nth-child(2) {
        font-size: 1em;
    }
    .main__about__description .column--50:nth-child(2) 
    .column__txt {
        line-height: 30px;
    }
    .column--50-25 {
        width: 24.5%;
    }
    .column__title {
        font-size: 1em;
    }
    .group__title {
        font-size: 2.2em;
        text-align: center;
    }

}
@media screen and (min-width:1024px) {
    .container {
        width: 1000px;
    }
    .logo{
        font-size: 1.8em;
        padding-left: 100px;
        font-family: 'Pacifico', cursiva;
        font-weight: 100;
    }
    .main-header__contactInfo__phone {
        color: white;
        margin: 0 auto;
        padding-right: 120px;
    }
    .main-header__contactInfo__address {
        padding-right: 30px;
        margin: 0;   
    }
    .main-nav {
        height:60px;
        padding: 0;
    }
    .banner__img {
        height: 700px;
    }
    .banner__content {
        font-size: 2.5em;
    }
    .icon-menu {
        display: none;
    }
    .menu {
        position: static;
        display: flex;
        height: 60px;
        width: auto;
    }
    .menu__link {
        padding: 20px;
    }
    .group--color .container {
        margin-top: 30px;
        margin-bottom: 30px;
        padding: 20px;
    }
    .main__title {
        font-size: 2.3em;
    }
    .main__about__description .column--50:nth-child(2) .column__txt {
        line-height: 50px;
    }
    .today-special__title {
        font-size: 1.4em;
    }
    .main-footer {
        padding-top: 30px;
        padding-bottom: 30px;
    }
} 

@media screen and (main-width:1600px) {
    .container {
        width: 1400px;
    }
    .main__about__description .column:nth-child(1) img {
        widows: 100%;
        height: 350px;
        object-fit: cover;
    }
    .today-special__img {
        height: 300px;
    }
}




